.blogContainer {
    margin-top: 80px;
    overflow: hidden;
}

.blogContainer h1 {
    font-size: 40px;
    color: #333;
    text-align: center;
    margin-bottom: 40px;
    overflow: hidden;
}

.blog-cardContainer {
    display: grid;
    justify-content: center;
    grid-template-columns: 450px 450px 450px;
    margin-left: 50px;
    overflow: hidden;
}

.BlogCard {
    margin-right: 30px;
    transition: ease-out 0.4s;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-bottom: 100px;
}

.blog-card-Details-1 {
    display: flex;
    flex-direction: row;
    height: 60px;
    margin-top: 10px;
    overflow: hidden;
}

.BlogCard:hover {
    position: relative;
    transform: translate(0, -10px);
    overflow: hidden;
}

.authorImg {
    height: 35px;
    width: 35px;
    overflow: hidden;
}

.blogposter {
    width: 100%;
    height: 300px;
    overflow: hidden;
}

.blog-card-Details {
    padding: 10px 10px 10px 10px;
    overflow: hidden;
}

.readmore {
    position: relative;
    left: 40%;
    color: #30813a;
    cursor: pointer;
    overflow: hidden;
}

.hooverDate {
    width: 30%;
    padding: 10px;
    position: absolute;
    bottom: 0;
    margin-bottom: 125px;
    left: 0;
    background-color: white;
    color: #30813a;
    text-align: center;
    opacity: 0;
    transition: 0.3s;
    overflow: hidden;
}

.BlogCard:hover .hooverDate {
    visibility: visible;
    opacity: 1;
    overflow: hidden;
}

.blog-card-Details-1 p {
    margin-left: 10px;
    font-size: 15px;
    overflow: hidden;
}


/*Blg detail */

.BlogDContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.blogListing {
    text-decoration: none;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    margin-top: 1.875rem;
    font-size: 1.25rem;
    overflow: hidden;
    gap: 20px;
    padding-bottom: 20px;
}

.Blogbanner {
    margin-top: 80px;
    margin-right: 20px;
}

.Blogbanner img {
    width: 100%;
}

.share {
    color: grey;
}

.facebook {
    color: #1cd94e;
}

.facebook:hover {
    cursor: pointer;
}

.twit {
    color: #1cd94e;
}

.twit:hover {
    cursor: pointer;
}

.link {
    color: #1cd94e;
}

.link:hover {
    cursor: pointer;
}

.inst {
    color: #1cd94e;
}

.inst:hover {
    cursor: pointer;
}

.btnnn {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* back ground-color: red; */
}

.btnnn button {
    background-color: #23db57;
    border: none;
    padding: 20px;
    gap: 20px;
    color: white;
    border-radius: 10px;
    transition: 0.2s;
}

#txtname {
    width: 800px;
    height: 45px;
    border: 1px solid green;
    outline: none;
    padding: 0 20px;
}

#formEmail {
    width: 800px;
    height: 45px;
    border: 1px solid green;
    outline: none;
    padding: 0 20px;
}

#formPhone {
    width: 800px;
    height: 45px;
    border: 1px solid green;
    outline: none;
