.postComment {
    background: #70d934;
    background-image: -webkit-linear-gradient(top, #1cd94e, #23db57);
    background-image: -moz-linear-gradient(top, #1cd94e, #23db57);
    background-image: -ms-linear-gradient(top, #1cd94e, #23db57);
    background-image: -o-linear-gradient(top, #1cd94e, #23db57);
    background-image: linear-gradient(to bottom, #1cd94e, #23db57);
    -webkit-border-radius: 2;
    -moz-border-radius: 2;
    border-radius: 2px;
    font-family: Georgia;
    color: #ffffff;
    font-size: 14px;
    padding: 18px 22px 15px 33px;
    border: solid #13f261 2px;
    text-decoration: none;
}

.postComment:hover {
    background: #fafafa;
    background-image: -webkit-linear-gradient(top, #fafafa, #fafafa);
    background-image: -moz-linear-gradient(top, #fafafa, #fafafa);
    background-image: -ms-linear-gradient(top, #fafafa, #fafafa);
    background-image: -o-linear-gradient(top, #fafafa, #fafafa);
    background-image: linear-gradient(to bottom, #fafafa, #fafafa);
    text-decoration: none;
    color: #13f261;
}

