.contact-form {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.contact-form-column {
    width: 53.125rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin-top: 6.25rem;
}

.contact-form-column div input {
    border: 0.0625rem solid #d6d5d5;
    height: 3.75rem;
    width: 25rem;
    border: solid 0.0625rem rgb(1, 217, 1);
    margin-bottom: 1.875rem;
    padding-left: 1.875rem;
    font-size: 0.9375rem;
    font-weight: 500;
    margin-left: 0.625rem;
    margin-right: 0.625rem;
    resize: none;
    outline: none;
}

.contact-form-items {
    display: flex;
    flex-direction: row;
}

.jobdesc-Input {
    border: 0.0625rem solid #d6d5d5;
    height: 9.375rem;
    width: 53.75rem;
    border-radius: 0;
    margin-bottom: 1.875rem;
    padding-left: 1.875rem;
    font-size: 0.9375rem;
    font-weight: 500;
    border: solid 0.0625rem rgb(1, 217, 1);
    resize: none;
    outline: none;
}

.Contact-btnSubmit {
    color: white;
    background: linear-gradient(90deg, #38a745 0%, #4cce5b 63%);
    border: none;
    width: 10.625rem;
    height: 3.125rem;
    font-size: 1rem;
    margin-left: 0.625rem;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
    font-size: 1rem;
}

.Contact-btnSubmit:hover {
    color: black;
    background: white;
    border: 0.0625rem solid #4bcc5a;
}

.Contact-btnDisabled {
    color: rgb(0, 0, 0);
    border: none;
    width: 10.625rem;
    height: 3.125rem;
    font-size: 1rem;
    margin-left: 0.625rem;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
    font-size: 1rem;
    color: white;
    cursor: not-allowed;
    background: linear-gradient(90deg, #38a745 0%, #4cce5b 63%);
}

.CheckBoxArea {
    display: flex;
    flex-direction: row;
    align-items: left;
    width: 100%;
    justify-content: flex-start;
    height: 50px;
    flex-wrap: wrap;
}

.googleMap-column {
    margin-top: 120px;
}